    font-size: 20px;
    line-height: 24px;
    margin-bottom: 5px;
}
.featured-info h5.title{
	font-size:20px;
	line-height: 28px;
	margin-bottom:2px;
}
.featured-info .title a{
	color:#fff;
}
.featured-info p{
	font-size:14px;
	color:rgba(255,255,255,0.8);
	margin-bottom: 10px;
}
