.fbottom-like .like-btn i:before,
.wish-bx li .like-btn i:before{
	content:"";
	content: "";
    width: 60px;
    height: 60px;
    display: block;
    position: absolute;
    left: -10px;
    top: -10px;
    display: inline-block;
    background: url(../images/like.png);
    cursor: pointer;
}
.fbottom-like .like-btn.active i:before,
